@import url(https://fonts.googleapis.com/css?family=Acme&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 12px;
}

.container {
    max-width: 1200px;
    margin: 0 auto; 
}


.nav-link{
    font-size: 15px;
}

.d-inline-block{
margin: 0 5px;
}

.grow:hover{
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
}

.about-wrapper {
    background: url(/portfolio/static/media/background.eb946522.jpg)

}
.text-wrapper {
    text-align: center;

}
.about-heading {
    text-align: center;
    padding: 36px 0 5px 0;
    font-family: 'Acme', sans-serif;
    font-size: 30px;
    color: #000;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    margin: 0;
}

.hr-line { 
    border: none;
    height: 20px;
    width: 70%;
    height: 70px;
    margin-top: 0;
    box-shadow: 0 20px 20px -20px #333;
    margin: -50px auto 10px; 
}

.image-wrapper {
    flex: 1 1;
    text-align: center;
}

.about-image-wrapper {
    display: flex;
    max-width: 800px;
    margin: 10px auto 0 auto;
    
}

.about-item {
    font-size: 10px;
    max-width: 640px;
    margin: auto;
}


@media only screen and (min-width: 600px) {
    .about-item {
        font-size: 15px;
    }
    .iw-image{
        width: 100%;
    }
}

.iw-image {
    width: 70%;
}
.image-text{
    font-size: 15px;
    padding: 5px 0 ;
}

.am-image{
    border-radius: 50%;
    padding-bottom: 10px;
}

.card {
    background-color: transparent;
    border: none;
}
.card-header {
    text-align: center;
}
.card-design {
    padding: 10px;
    align-items: center;
}

.card-img-top {
    width: 60%;
}

@media only screen and (min-width: 600px) {
    .card-design {
        width: 24%;
    }

    .card-wrapper {
        display: flex;
        flex-wrap: wrap;
    }

    .card-img-top {
        width: 100%;
    }
}
.banner-image {
    vertical-align: middle;
    display: block;
    max-width: 100%;
    height: auto;
    margin: 0 auto;
    width: 100%;
}

